import { Link } from 'react-router-dom';
import classNames from 'classnames/bind';

import Grid from '../Grid';
import logo from '../../assets/images/logo-2.png';
import style from './footer.scss';

const cx = classNames.bind(style);

const footerAboutLinks = [
    { display: 'Gii Thiu', path: '/about-us' },
    { display: 'Lin H', path: '/contact' },
    { display: 'Tin Tc', path: '/about-us' },
];

const footerCustomerLinks = [
    { display: 'Chnh Sch i Tr', path: '/customer-care/returnGoods' },
    { display: 'Chnh Sch Bo Hnh', path: '/customer-care/insurance' },
    { display: 'Chnh Sch Hon Tin', path: '/customer-care/returnMoney' },
];

function Footer() {
    return (
        <div>
            <footer className={cx('footer')}>
                <div className={cx('container')}>
                    <Grid className={cx('footer__box')} col={4} mdCol={2} smCol={1} gap={10}>
                        <div>
                            <h4 className={cx('footer__title')}>Tng i H Tr</h4>
                            <div className={cx('footer__content')}>
                                <p>
                                    Lin H t Hng: <strong>0999999999</strong>
                                </p>
                                <p>
                                    Thc Mc n Hng: <a target='blank' href='https://www.facebook.com/jemco.closet.vn/'>Lin h</a>
                                </p>
                                <p>
                                    Gp , Khiu Ni: <strong>trungvibui123@gmai.com</strong>
                                </p>
                            </div>
                        </div>
                        <div>
                            <h4 className={cx('footer__title')}>V Jemco</h4>
                            <div className={cx('footer__content')}>
                                {footerAboutLinks.map((item, index) => (
                                    <p key={index}>
                                        <Link to={item.path}>{item.display}</Link>
                                    </p>
                                ))}
                            </div>
                        </div>

                        <div >
                            <h4 className={cx('footer__title')}>Chm Sc Khch Hng</h4>
                            <div className={cx('footer__content')}>
                                {footerCustomerLinks.map((item, index) => (
                                    <p key={index}>
                                        <Link to={item.path}>{item.display}</Link>
                                    </p>
                                ))}
                            </div>
                        </div>

                        <div >
                            <Link to={'/'} className={cx('footer__logo__box')}>
                                <img className={cx('footer__logo')} src={logo} alt="footer__logo" />
                            </Link>
                            <p>
                                Hng n mc tiu mang li nim vui n mc mi mi ngy cho hng triu ngi tiu dng
                                Vit. Hy cng Jemco hng n mt cuc sng nng ng, tch cc hn.
                            </p>
                        </div>
                    </Grid>
                </div>
            </footer>
        </div>
    );
}

export default Footer;
